.Gallery{
}
.Gallery .LNB{
    z-index: 3;
    position: fixed;
    top : 50px;
}
.Gallery .section{
    width : 100vw;
}
.Gallery .section .wrapper{
    margin : 0 auto;
    width : 800px;
    padding : 60px 0 80px 0;
}
.Gallery .section .title{
    width : fit-content;
    margin : 0 auto;
    padding : 0 8px;

    position: relative;
    top : -12px;
    background-color: white;
}
.Gallery .section .title .text{
    font-size: 16px;
    font-weight: 700;
    text-align: center;
}

.Gallery .line {
    width : 100vw;
    height : 1px;
    min-height: 1px;
}
/* -------------------------------------- */
.Gallery .intro {
    margin-top: 53px;
}
.Gallery .intro .title{
    font-size: 24px;
    font-weight: 800;
    text-align: center;
    margin-bottom: 15px;
}
.Gallery .intro .genre{
    font-size: 16px;
    text-align: center;
    margin-bottom: 28px;
}
.Gallery .intro .video{
    margin-bottom: 53px;
}
.Gallery .intro .desc{
    font-size: 16px;
    line-height: 1.88;
}
/* -------------------------------------- */
.Gallery .detail .wrapper{
    margin-bottom : 20px;
}
.Gallery .detail .item{
    margin-bottom : 40px;
}
.Gallery .detail .item img{
    width : 100%;
    margin-bottom : 40px;
}
.Gallery .detail .desc{
    font-size: 16px;
    line-height: 1.88;
}
/* -------------------------------------- */
.Gallery .artist .wrapper{
    padding-top: 12px;
    padding-bottom : 130px;
}
.Gallery .artist .teamName{
    font-size: 20px;
    font-weight: 800;
    text-align: center;
}
.Gallery .artist .list{
    display: flex;
    max-width: 600px;
    height : 450px;
    margin : 38px auto 0 auto;

    flex-direction : row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
.Gallery .artist .list .item{
    text-align: center;
}
.Gallery .artist .list .item .name{
    margin : 16px 0 1px 0;
    font-size: 16px;
    font-weight: 700;
}
.Gallery .artist .list .item .contact{
    font-size: 16px;
    margin-bottom: 46px;
}
.Gallery .artist .list .item .profile{
    width : 136px;
    height : 136px;
    border-radius: 78px;
    background-size: contain;
    border : solid 1px ;
}
/* -------------------------------------- */
.Gallery .goBack{
    width : 290px;
    height : 67px;
    border : 2px solid;
    margin : 0 auto 86px auto;


    display: flex;
    justify-content: center;
    align-items: center;
}
.Gallery .goBack .text{
    font-size: 24px;
    font-weight: 800;
}